#btn-meg{
    background: url('../../resources/images/btn/meg.png') no-repeat center center/ cover;
}

.count{

    width: 300px;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    &__btn-score{

        width: 100%;
        height: 300px;

        margin-bottom: 32px;

    }

    &__btn-realism{

        width: 35px;
        height: 20px;

        border: 1px solid black;
        border-radius: 20px;

        position: relative;

        &__circle{

            position: absolute;
            top: 0;
            left: 0;

            width: 20px;
            height: 100%;

            border-radius: 50%;

            background-color: black;

            transition: all .7s ease;

        }

    }

    &__text{

        font-weight: 600;
        font-size: 24px;

        margin-bottom: 20px;
    }

    &__text--weight{

        font-weight: 400;
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 2000px) {
    .count{

        width: 500px;
        
        &__btn-score{
    
            height: 500px;
    
        }
    
        &__text{

            font-weight: 600;
            font-size: 24px;
    
            margin-bottom: 20px;
        }
    
        &__text--weight{
    
            font-weight: 400;
            margin-bottom: 10px;
        }
    }
}

@media screen and (max-width: 1400px) {
    .count{

        width: 400px;
        
        &__btn-score{
    
            height: 400px;
    
        }
    
        &__text{

            font-weight: 600;
            font-size: 20px;
    
            margin-bottom: 20px;
        }
    
        &__text--weight{
    
            font-weight: 400;
            margin-bottom: 10px;
        }
    }
}